import React, { Component, Suspense } from 'react';
import { Accordion, OverlayTrigger, Tooltip } from 'react-bootstrap';
import $ from 'jquery';
import { Uppy } from "@uppy/core";
import Transloadit from "@uppy/transloadit";
import GoogleDrive from '@uppy/google-drive';
import Url from '@uppy/url';
import { DashboardModal } from '@uppy/react';
const ColorPicker = React.lazy(() => import('../../../../CommonComponents/ColorPicker'));

const uppyConfig = {
    uppyKey: '0701d3d7f8d94e92a65a9cba388b1d96',
    uppyBucket: 'cloud.taggbox.com',
    uppyAwsKey: '47ABVORKG52UYE1KB28S',
    uppyAwsSecret: 'pcaTfeokaz28geAAns08H3yaGbOVz6ILgTptu19v',
    companionUrl: 'https://test.taggbox.com'
}
class Background extends Component {
    constructor(props) {
        super(props)
        this.uppy = new Uppy({ debug: true, autoProceed: false, restrictions: { maxFileSize: 5000000, maxNumberOfFiles: 1, minNumberOfFiles: 1, allowedFileTypes: ['image/*'] } })
            .use(Transloadit, {
                target: '#a',
                params: {
                    auth: {
                        key: uppyConfig.uppyKey
                    },
                    steps: {
                        store: {
                            robot: "/wasabi/store",
                            bucket_region: "us-west-1",
                            bucket: uppyConfig.uppyBucket,
                            key: uppyConfig.uppyAwsKey,
                            secret: uppyConfig.uppyAwsSecret,
                            path: 'postimage/${file.id}'
                        }
                    }
                },
                waitForEncoding: true
            })
            .use(GoogleDrive, { target: Uppy.Dashboard, companionUrl: uppyConfig.companionUrl })
            .use(Url, { target: Uppy.Dashboard, companionUrl: uppyConfig.companionUrl })
    }
    state = {
        backgroundModal: false
    }
    componentDidMount() {
        var self = this;
        this.uppy.on('transloadit:result', (stepName, result) => {
            self.props.onClickTouploadImage(`https://cloud.taggbox.com/postimage/${result.id}`, result.id, () => {
                setTimeout(() => {
                    this.uppy.cancelAll()
                    this.uppy.removeFile()
                    this.setState({ backgroundModal: false })
                }, 300)
            })
        })
    }
    onBackgroundClose = event => {
        this.uppy.cancelAll()
        this.uppy.removeFile()
        this.setState({ backgroundModal: false })
    }
    onBackgroundOpen = event => {
        this.setState({ backgroundModal: true }, () => {
            $(".uppy-Dashboard-close").click(() => {
                this.uppy.cancelAll()
                this.uppy.removeFile()
                this.setState({ backgroundModal: false })
            })
        })
    }
    render() {
        const { onClickEnableBackground, onClickBackgroundColor, background, backgroundImageRemove } = this.props
        const { backgroundModal } = this.state
        return <>

            <Accordion.Item eventKey="2" className="pb-3">
                <Accordion.Header className="w-100">
                    <span className="fs-9 fw-medium">Background</span>
                </Accordion.Header>
                <Accordion.Body className="border rounded-1 mt-1 p-2">
                    <label className={`form-check form-switch py-2 align-items-center justify-content-between ${background.status ? `border-bottom` : ``} h-40px py-1`}>
                        <span className="form-check-label ps-0 fs-9 fw-medium text-gray-800">Enable Background</span>
                        <input className="form-check-input" type="checkbox" name="autoplayVideos" checked={background.status} onChange={onClickEnableBackground} />
                    </label>
                    {
                        background.status ? <><label className="d-flex align-items-center justify-content-between border-bottom h-40px py-1 position-relative">
                            <span className="ms-0 fs-9 fw-medium">Background Color</span>
                            <Suspense fallback={<></>}>
                                <ColorPicker color={background.color} onChangeColor={onClickBackgroundColor} />
                            </Suspense>
                        </label>
                            <div className="d-flex align-items-start justify-content-between flex-column py-1">
                                <p className="fs-9 fw-medium text-start mt-2">Upload Background Image</p>
                                <button type="button" className="btn btn-secondary btn-sm w-100 mb-2" onClick={this.onBackgroundOpen}>Attach Image</button>
                                {background.image ? <div className="card card-custom w-100 rounded-1">
                                    <div className="card-body d-flex flex-column p-1">
                                        <div className="position-relative d-flex align-items-center cursor-pointer">
                                            <div className="position-relative rounded-1 bg-light overflow-hidden w-100" style={{ paddingBottom: '66%' }}>
                                                <img className="object-center-contain position-absolute" loading="lazy" src={background.image} height={500} width={300} alt={'suit'} />
                                            </div>
                                            <div className="removeBanner position-absolute end-0 top-0 pt-2 pe-2">
                                                <div className="d-flex align-items-center justify-content-end">
                                                    <OverlayTrigger placement="top" overlay={<Tooltip>Edit</Tooltip>}>
                                                        <button type="button" className="btn btn-icon btn-secondary h-26px w-26px me-2" onClick={this.onBackgroundOpen}>
                                                            <i className="ri-edit-box-line text-gray-700 me-0"></i>
                                                        </button>
                                                    </OverlayTrigger>
                                                    <OverlayTrigger placement="top" overlay={<Tooltip>Remove</Tooltip>}>
                                                        <button type="button" className="btn btn-icon btn-secondary h-26px w-26px" onClick={backgroundImageRemove}>
                                                            <i className="ri-delete-bin-6-line text-gray-700 me-0"></i>
                                                        </button>
                                                    </OverlayTrigger>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div> : null}
                            </div></> : null
                    }

                </Accordion.Body>
            </Accordion.Item>
            {
                backgroundModal ? <DashboardModal
                    animateOpenClose={true}
                    proudlyDisplayPoweredByUppy={false}
                    inline={false}
                    showProgressDetails={true}
                    height={750}
                    browserBackButtonClose={true}
                    showRemoveButtonAfterComplete={false}
                    uppy={this.uppy}
                    open={true}
                    onRequestCloseModal={this.onBackgroundClose}
                    closeAfterFinish={true}
                    plugins={[]}
                /> : null
            }
        </>
    }

}
export default Background